import { LoadingComponent } from './../../loading/loading.component';
import { MessagesComponent } from './../../messages/messages.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthenticationService } from './../../../services/authentication.service';
import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public formLogin: FormGroup;

  constructor(private authService: AuthenticationService, private router: Router) { }

  ngOnInit(): void {
    this.formLogin = new FormGroup(
      {
        username: new FormControl("", [Validators.required, Validators.email, Validators.maxLength(100)]),
        password: new FormControl("", [Validators.required, Validators.maxLength(100)])
      }
    );
  }

  public login(): void {
    LoadingComponent.isActive = true;
    this.authService.login(this.formLogin.getRawValue()).subscribe(
      response => {
        sessionStorage.removeItem("token");
        sessionStorage.setItem("token", response.token);
        this.router.navigate(["/admin/products"]);
        swal.fire("¡Bienvenido!", "", "success");
        LoadingComponent.isActive = false;
      },
      error => {
        MessagesComponent.errorMessage(error);
      }
    );
  }

}
