import { LoadingComponent } from './../loading/loading.component';
import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  public static errorMessage(error: any): void {
    console.log(error);
    if (error.error.error) {
      swal.fire(error.error.error, "", "error");
    } else if (error.error) {
      let errorHtml: string = "<ul>";
      for (let errori of error.error) {
        errorHtml = errorHtml + "<li>" + errori + "</li>";
      }
      errorHtml = errorHtml + "</ul>";
      swal.fire({
        title: "¡Error!",
        icon: "error",
        html: errorHtml
      });
    } else {
      swal.fire("¡Upps!", "¡Tenemos un error interno, por favor intenta mas tarde!", "error");
    }
    LoadingComponent.isActive = false;
  }

}
