import { environment } from './../../environments/environment.prod';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  constructor(private http: HttpClient) { }

  public login(authData: any): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/login", authData).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public validToken(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/token/valid?token=" + sessionStorage.getItem("token")).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
