import { AuthenticationValidGuard } from './guards/authentication-valid.guard';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { CatalogueComponent } from './components/pages/catalogue/catalogue.component';
import { InformationComponent } from './components/pages/information/information.component';
import { LoginComponent } from './components/pages/login/login.component';

const routes: Routes = [
  {
    path: "", redirectTo: "/catalogue", pathMatch: "full"
  },
  {
    path: "information", component: InformationComponent
  },
  {
    path: "catalogue", component: CatalogueComponent
  },
  {
    path: "admin", canActivate: [AuthenticationValidGuard], loadChildren: () => import("./components/pages/admin/admin.module").then(m => m.AdminModule)
  },
  {
    path: "login", component: LoginComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
