import { Router } from '@angular/router';
import { SessionService } from './../../../utils/session-service';
import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';

interface Menu {
  text: string,
  route: string,
  icon: string;
}

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  public isMenuCollapsed: boolean = true;
  public static MENU: Menu[];
  public static MENU_GENERAL: Menu[] = [
    {
      text: "Catalogo",
      route: "/catalogue",
      icon: "fa-solid fa-boxes-stacked"
    },
    {
      text: "Información",
      route: "/information",
      icon: "fa-solid fa-circle-info"
    },
    {
      text: "Ingresar",
      route: "/login",
      icon: "fa-solid fa-right-to-bracket"
    }
  ];
  public static MENU_ADMIN: Menu[] = [
    {
      text: "Productos",
      route: "/admin/products",
      icon: "fa-solid fa-boxes-stacked"
    }
  ];
  public sessionService: SessionService;

  constructor(private router: Router) { }

  ngOnInit(): void {
    NavbarComponent.MENU = NavbarComponent.MENU_GENERAL;
    this.sessionService = new SessionService();
  }

  public getMenu(): Menu[]{
    return NavbarComponent.MENU;
  }

  public logout(): void {
    this.sessionService.deleteToken();
    NavbarComponent.MENU = NavbarComponent.MENU_GENERAL;
    this.router.navigate(["/"]);
    swal.fire("¡Adiós regresa pronto!", "", "success");
  }

}
