import { NavbarComponent } from './../components/layout/navbar/navbar.component';
import { SessionService } from './../utils/session-service';
import { LoadingComponent } from './../components/loading/loading.component';
import { MessagesComponent } from './../components/messages/messages.component';
import { AuthenticationService } from './../services/authentication.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Route, Router } from '@angular/router';
import { Observable } from 'rxjs';
import swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationValidGuard implements CanActivate {

  private sessionService: SessionService;

  constructor(private authentication: AuthenticationService, private routerService: Router){
    this.sessionService = new SessionService();
  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if(this.sessionService.isAuthentication()) {
      LoadingComponent.isActive = true;
      this.authentication.validToken().subscribe(
        response => {
          if(!response.isValid) {
            this.sessionService.deleteToken();
            swal.fire("¡No tienes permiso para ingresar a este recurso!", "", "warning");
            this.routerService.navigate(["/"]);
          } else {
            NavbarComponent.MENU = NavbarComponent.MENU_ADMIN;
          }
          LoadingComponent.isActive = false;
        },
        error => {
          MessagesComponent.errorMessage(error);
        }
      );
      return true;
    }
    this.routerService.navigate(["/"]);
    swal.fire("¡Por favor inicia sesión!", "", "info");
    return false;
  }
  
}
